import userServices from '../apis/users/services/index';

export default async function addLoggedInUserInRequestObject(req, res, next) {
  try {
    const loggedInUser = await userServices.getUserByUserId(req.user.userId);
    req.loggedInUser = loggedInUser;
    next();
  } catch (err) {
    next(err);
  }
}
